}

.register_form {
    position: absolute;
    /*left: 140px;*/
    left: 10%;
    top: 150px;
    background-color: white;
    padding: 30px 20px 30px 20px;
    border-radius: 10px;
    box-shadow: 0 12px 15px 0 rgba(0,0,0,0.2),0 7px 5px 0 rgba(0,0,0,0.1);
    z-index:2;
}

.register_input {
    border-radius: 5px;
    border-color: darkgrey;
    border-style: solid;
    height: 30px;
    width: 240px;
    margin: 5px 0px 20px 0px;
    padding-left: 10px;
}

.register_button {
    border: none;
    border-radius: 8px;
    height: 40px;
    background-color: #00A3E7;
    color: white;
    padding: 5px 55px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 4px 2px;
    transition-duration: 0.4s;
    cursor: pointer;
    box-shadow: 0 5px 5px 0 rgba(0,0,0,0.2),0 7px 5px 0 rgba(0,0,0,0.1);
}

.register_button:hover {
    background-color: lightskyblue;
    /*color: black;*/
}

